.films {
    margin: 0 auto;
    width: 100%;
    max-width: 1140px;
    display: grid;
    grid-template-columns: repeat(3, 364px);
    gap: 30px 24px;
    padding-top: 80px;
    border-top: 1px solid #E8E8E8;
}

.films_zero-films {
    height: calc(371px);
}

.films_btn-disabled {
    margin-bottom: 140px;
}

@media screen and (max-width: 1150px) {

.films {
    max-width: 708px;
    grid-template-columns: repeat(2, 339px);
    gap: 36px 30px;
}

.films_zero-films {
    height: calc(652px);
}


.films_btn-disabled {
    margin-bottom: 80px;
}

}


@media screen and (max-width: 720px) {

.films {
    max-width: 300px;
    grid-template-columns: repeat(1, 300px);
    gap: 16px;
    padding-top: 29px;
}

.films_zero-films {
    height: 558px;
}
}